*{
    padding: 0;
    margin: 0;
}
a{
    margin-left: 10px;
}
body{
    background-color: #ccc;
}
.header{
    height: 50px;
    background-color: #444;
}
.header header{
    width: 1200px;
    line-height: 50px;
    margin: 0 auto;
}
.header header .search{
    width:233px;
    height: 28px;
    border-radius: 14px;
    background-color: #fff;
    display: inline-block;
    margin: 10px 0 0 30px;
}
.header header .search img{
    float: left;
    padding: 5px 0 0 0;
    width: 22px;
    height: 22px;
}
.header header .search input{
    width:204px;
    height: 25px;
    float: right;
    margin: 2px 7px 0 0;
    /* padding: 0 0 5px 0; */
    border: 0;
}
.header header a{
    margin: 0 20px;
    color: #fff;
}
.header img{
    width: 120px;
}

/* content */
.content{
    width: 1200px;
    margin: 30px auto 0;
}
.left{
    width: 900px;
    float: left;
}
.right{
    width: 270px;
    float: right
}